
.App-viewer {
	flex-grow: 1;
	padding: 15px;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	gap: 15px;
	transition: all 0.5s ease;
	overflow: overlay;
}

.wrapper{
	flex-grow: 1;
	height: 100%;
	overflow: overlay;

}
.main{
	display: flex;
	height: 100%;
	padding: 25px 15px;
	overflow: overlay;
}
.main-content{
	padding:15px;
	flex-grow: 1;
	height: 100%;
	overflow: overlay;
}