.wrapper{
    max-width: 320px;
    padding: 0 16px;
    overflow: overlay;
    border-left: 2px solid var(--border-color);
}
.section_top{
    border-bottom: 2px solid var(--border-color);
    margin-bottom: 15px;
    padding: 16px 0;
}
.section_styles{
    padding: 16px 0;
}
.props-input{
    max-width: 125px;
}
.text_input{
     -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    width: 100%;
    padding: 15px;
	border: 1px solid var(--border-color);
	color: inherit;
	font-size: 1.25rem;
    resize:vertical;
    margin-bottom: 25px;
}

.section_heading{
    font-weight: 700;
    text-transform: capitalize;
    padding: 8px 0px;
}
.flex-col{
    display: flex;
    flex-direction: column;
    padding-left: 8px;
}

.flex-row{
       display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    padding: 8px;
}
.btn-remove-element{
     -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 8px 12px;
    border: 1px solid var(--border-color);
    border-radius: 3px;
    cursor: pointer;
    transition: color 250ms ease;
    color: var(--text-color);
    &:hover{
        color: red;
    }
}
.add-btn{
     -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 8px 12px;
    border: 1px solid var(--border-color);
    border-radius: 3px;
    display: block;
    color: var(--text-color);
    font-size: 1.25rem;
    cursor: pointer;
     &:hover{
        color: var(--LimeGreen);
        border-color: var(--LimeGreen);
    }
}
.group-btns{
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    .tag-btn{
     -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 8px 12px;
    border: 1px solid var(--border-color);
    border-radius: 3px;
    cursor: pointer;
    transition: color 250ms ease;
    color: var(--text-color);
    &:active{
        color: red;
    }
    &:hover{
        color: var(--LimeGreen);
        border-color: var(--LimeGreen);
    }
}
}

.row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 15px;
    padding: 8px;
    &>input{
    -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
    padding: 3px;
	border: none;
	border: 1px dashed var(--border-color);
	color: var(--text-color);
	font-size: 1rem;
}
}
.selecedElement{
    outline:1px dashed var(--BrightRed);
    outline-offset: 8px;
}

.select-wrapper{
    -webkit-appearance: none;
	appearance: none;
    background-color: transparent;
    border: 2px solid var(--border-color);
    border-radius: 3px;
    padding: 3px 6px;
    font-size: 1.25rem;
    color: inherit;
    cursor: pointer;
}
.option{
    -webkit-appearance: none;
	appearance: none;
    padding: 3px 6px;
    font-size: 1.25rem;
    color: inherit;
    background-color: transparent;
    &:hover{
        background-color: var(--background-sec);
    }
}