.wrapper{
    background-color: var(--background-sec);
    padding:16px 8px;
    min-height: 50px;
	box-shadow: rgb(0 0 0 / 12%) 0px 1px 1px;
    display: flex;
    gap: 8px;
    border-radius: 3px;
    .tabs{
        flex-grow: 1;
        border-right: 1px solid var(--border-color);
        display: flex;
        gap: 8px;
        overflow-x: auto;
    }
}

.Enable_fn_btns{
    margin-left: auto;
    display: flex;
    align-items: center;
    padding-right: 16px;
    gap: 15px;
    &>div>label{    
        margin-left: 5px;
        cursor: pointer;
        text-transform: capitalize;
     }
}

.tab_btn_wraper{
    border-right: 1px solid rgb(0 0 0 / 12%);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 16px;
    border-bottom: 1px solid var(--border-color);
    &.tabActive{
        border-color: var(--LimeGreen);
    }
}
.tab_btn_wraper:hover .tab_btn_delete{
    visibility: visible;
}
.tab_btn {
     text-transform: capitalize;
	transition: color 0.5s ease;
	background-color: transparent;
	font-size: 1rem;
	min-height: 16px;
	cursor: pointer;
    border: none;
	color: inherit;
	&:hover {
		color: var(--LimeGreen);
	}
	&[tabindex]:focus {
		border-color: transparent;
		outline: 2px var(--LimeGreen) solid;
	}
    &.tabActive{
    border-bottom-color: var(--LimeGreen);
}
}
.tab_btn_delete{
    margin-left: 8px;
    transition: all 0.5s ease; 
    display: inline-flex;
    visibility: hidden;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
	font-size: 1rem;
	cursor: pointer;
    border: none;
	color: var(--BrightRed);
    &:hover{
        filter: brightness(1.25);
    }
}
