.wrapper {
	width: 100%;
	max-width: 320px;
	height: inherit;
	border-left: 2px solid var(--border-color);
	display: flex;
	flex-direction: column;
	overflow: overlay;
	user-select: none;
}

.tab_btnStyle {
	transition: color 0.5s ease;
	background-color: transparent;
	font-size: 1rem;
	min-height: 16px;
	cursor: pointer;
    padding: 3px 6px;
    border: none;
	border-radius: 3px;
	border-bottom: 2px solid var(--border-color);
	color: inherit;
	&:not(:last-child) {
		margin-right: 15px;
	}
	&:hover {
		color: var(--LimeGreen);
	}
	&[tabindex]:focus {
		border-color: transparent;
		outline: 2px var(--LimeGreen) solid;
	}
    &.tabActive{
    border-bottom-color: var(--LimeGreen);
}
}

@media screen and (min-width: 920px) {
	.wrapper {
		// width: 320px;
	}
}
