.wrapper {
	display: flex;
	flex-direction: column;
	height: 100%;
	overflow: overlay;
}
.Note_wrapper {
	flex-grow: 1;
	padding: 15px;
	list-style: none;
	overflow: overlay;
}
.paginations-wrapper{
	text-align: center;
	padding: 8px;
	border-bottom: 2px solid  var(--border-color);
}
.pagination-input-wrraper{
	display: inline;
}
.paginations-modify-page-input{
	  -webkit-appearance: none;
	appearance: none;
	background-color: transparent;
		border: none;
		outline: none;
		color: inherit;
		font-size: 0.75rem;
		width: 45px;
		padding: 2px 6px;
		border-radius: 3px;
		outline:  2px transparent solid;;
		&:focus {
	outline-color: var(--LimeGreen);
	}
}

.Item {
	display: flex;
	flex-direction: column;
	background: var(--background-sec);
	cursor: pointer;
	transition: background 250ms linear, color 250ms linear;
	border-radius: 6px;
	font-size: 1.25rem;
	&:not(:last-child) {
		margin-bottom: 3px;
		border-bottom: 1px solid rgb(0 0 0 / 12%);
	}
	&:hover {
		background-color: var(--border-color);
		color: var(--white);
	}
	.folder-wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 8px 16px;
		gap: 15px;
	}
	.item-btns {
		display: flex;
		gap: 5px;
        .tippy-wrapper{
            background-color: burlywood;
            padding: 15px;
            border-radius:6px;
        }
        .create_subFolder_input{
            display: block;
            padding: 3px 6px;
            border-radius: 3px;
        }
	}
	.btn {
		background-color: transparent;
		border: none;
		outline: none;
		color: inherit;
		font-size: 0.75rem;
		cursor: pointer;
		padding: 5px;
		border: 1px solid var(--border-color);
		width: 25px;
		height: auto;
		border-radius: 3px;
		transition: color 250ms linear;
		&:disabled {
			cursor: not-allowed;
			opacity: 0.5;
		}
		&:not(:disabled):hover {
			color: var(--LimeGreen);
			border-color: var(--text-color);
		}
		&:not(:disabled):last-child:hover {
			color: var(--BrightRed);
		}
		&[tabindex]:focus {
			outline-color: var(--LimeGreen);
		}
	}
}

.sub-folder-wrapper {
	list-style-type: none;
    padding: 8px;
    border-top: 1px solid rgb(0 0 0 / 12%);
	.sub_item {
        display: flex;
        justify-content: space-between;
		cursor: pointer;
		transition: background 250ms linear, color 250ms linear;
        padding: 3px;
	    padding-left: 35px;
	    border-radius: 6px;
        span{
            flex-grow: 1;
        }
		&:not(:last-child) {
			margin-bottom: 5px;
			border-bottom: 1px solid rgb(0 0 0 / 12%);
		}
		&:hover {
			background-color: var(--background-sec);
			color: var(--text-color);
		}
	}
}
.folder-lable {
	width: 80%;
	&[contenteditable] {
		outline: 0px solid transparent;
	}
}


