// lap lai code



.context_menu_warpper {
	background-color: var(--background-sec);
	padding: 16px 8px;
	border: 1px solid var(--border-color);
	border-radius: 8px;
	z-index: 999;
}
.context_menu_item {
	padding: 3px 8px;
	cursor: pointer;
	transition: background 250ms linear, color 250ms linear;
	border-radius: 3px;
	font-size: 1.1rem;
	&:not(:last-child) {
		margin-bottom: 3px;
		border-bottom: 1px solid rgb(0 0 0 / 12%);
	}
	&:hover {
		background-color: var(--border-color);
		color: var(--white);
	}
}
.input-wrraper {
	padding: 3px 8px;
	width: 88%;
	margin: 0 auto;
	.input {
		-webkit-appearance: none;
		appearance: none;
		background-color: transparent;
		font-size: 1rem;
		font-weight: 300;
		border: 2px solid var(--border-color);
		padding: 6px 8px;
		flex-grow: 1;
		width: 100%;
		height: 100%;
		color: inherit;
		border-radius: 3px;
		outline: 2px transparent solid;
		&[tabindex]:focus {
			outline-color: var(--LimeGreen);
		}
	}
}